.page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: opacity 0.5s ease;
  overflow: auto;
  &.show {
    opacity: 1;
    transition: opacity 0.3s ease;
  }
  & > .valign-wrapper {
    height: 100%;
  }

}



