// for invalid hint bootstrup
input.invalid + label {
  &:after {
    display: none;
  }
  &.active {
    width: 100%;

    &:after {
      display: block;
      top: 60px;
    }
  }
}

.input-field {
  position: relative;
  label.active {
    top: -1.2rem;
  }
}

.csstransforms {
  .input-field label.active {
    top: 0.8rem;;
    transform: translateY(-25px);
  }

}





