.index-page {
	.bg{
		background: #26a69a;
		border-radius: 2px;
		color: #fff;
	}
	.column {
	    margin:0px;
	    display: inline-block;
	    vertical-align: bottom;
	    width: 60%;
	    line-height: 1;
	}
	.icon {
	    display: block;
	    width: 60px;
	    margin: 5px;    
	    float: left;
	}
	.row-help{
		margin-bottom: 5px;
	}
}



